import { Button } from '@material-ui/core'
import React from 'react'
import { Row } from './chooseMethod_styles'
import readExel from 'read-excel-file'
import { generate as makeId } from 'shortid'
import localForage from 'localforage'
import Header from '../header/Header'
import { Container, P, Page, PTitle } from '../pages/home/home_styles'
import { Link } from 'react-router-dom'

const ChooseMethod = ({ setIsFirstTime }) => {
  return (
    <>
      <Header />

      <Container>
        <Page>
          <PTitle>Alamar Blue Test</PTitle>

          <P style={{ marginBottom: '4em' }}>
            AlamarBlue is a cell viability assay reagent which contains the cell
            permeable, non-toxic, and weakly fluorescent blue indicator dye
            called resazurin. This is a trusted and established reagent which
            has been available since 1993. alamarBlue is a useful non-toxic
            alternative to the commonly used MTT cell viability assay
            (3-(4,5-dimethylthiazol-2-yl)-2,5-diphenyl-tetrazolium bromide)
            (Oâ€™Brien et al. 2000). alamarBlue quantitatively measures
            proliferation in human, animal, bacterial, fungal, and mycobacterial
            cells. It is useful for cytokine bioassays, cell viability assays,
            and in vitro cytotoxicity determinations as well as cell growth
            monitoring.
          </P>

          <PTitle>How do you want to input your data?</PTitle>

          <P style={{ marginBottom: '2em' }}>
            You can either input your parameters manually or you can use the
            test results excel file generated by your microplate reader.
            Technically you should be able to use any excel file but at the
            moment the formatting of the file should like this:
            <Link
              to='/sample.xlsx'
              target='_blank'
              download
              style={{ color: '#38afff', marginLeft: '1em' }}
            >
              SAMPLE EXCEL FILE
            </Link>
          </P>

          <Row>
            <Button
              variant='contained'
              style={{ width: '13em', marginRight: '2em' }}
              onClick={async () => {
                const base = await localForage.getItem('base')
                await localForage.setItem('base', {
                  ...base,
                  isFirstTime: false,
                })

                setIsFirstTime(false)
              }}
            >
              Manually
            </Button>

            <Button
              variant='contained'
              component='label'
              style={{
                width: '13em',
                backgroundColor: '#38afff',
                color: 'white',
              }}
            >
              <input
                type='file'
                hidden
                onChange={async ({ target: { files } }) => {
                  const fileData = await readExel(files[0])
                  const parsedData = fileData.map(row =>
                    row.map(cell => ({ id: makeId(), data: cell }))
                  )

                  const base = await localForage.getItem('base')
                  await localForage.setItem('base', {
                    ...base,
                    excelData: parsedData,
                    isFirstTime: false,
                  })

                  setIsFirstTime(false)
                }}
              />
              From Excel File
            </Button>
          </Row>
        </Page>
      </Container>
    </>
  )
}

export default ChooseMethod
